<template>
  <div class="">  
    <div class=" bg-gray-800  h-16 px-2 py-2 md:nav_wrapper md:flex md:items-center md:h-16 md:justify-between md:w-screen md:bg-gray-800" >
      <!-- LOGO ,LOGO IMG AND BURGER ICON(FOR MOBILE) WRAPPER START -->
      <div class="flex justify-between items-center ">
      <div class="flex items-center">
        <img src="../../public/img/logo.png" alt="logo" class=" w-12" />
        <router-link to="/"><p class="logo_text text-white text-xl font-logo_font uppercase">share_zone</p></router-link>
      </div>
      <!-- BURGER ICON WRAPPER START  -->
      <div  @click="burgerFunc()" class="burger-wrapper relative" ref="burger_wrapper_ref">
      <div class="burger block cursor-pointer relative md:hidden" ref="burger_ref">
        <div>
          <span class="nav_span">&nbsp;</span>
          <span class="nav_span">&nbsp;</span>
          <span class="nav_span">&nbsp;</span>
          <span class="nav_span">&nbsp;</span>
        </div>
      </div>
    </div>
    </div>
      <!-- LOGO AND LOGO IMG WRAPPER END -->
      <!-- NAV ELEMENTS UL WRAPPER START -->
      <ul class="hidden nav_elements text-white font-bold md:flex md:text-2xl md:font-mono md:text-white md:font-navbar_font" ref="navbar_elements">
        <li class="md: pt-4 md:px-12 md:pt-4"><router-link to="/">HOME</router-link></li>
        <li class="md: pt-4 md:px-12 md:pt-4"><router-link :to="{name:'Explore'}">EXPLORE</router-link></li>
        <li class="md: pt-4 md:px-12 md:pt-4"><a href="#">SEARCH</a></li>
        <li class="md: pt-4 md:px-12 md:pt-4"><router-link :to="{name:'Library'}">LIBRARY</router-link></li>
      </ul>
      <div class="login_signup_mobile hidden text-black mt-4 flex" ref="login_signup_mobile_btns">
        <div><a href="#" class="font-bold" data-tooltip="LOGIN">LOGIN</a></div>
        <div><a href="#" class="p-4 font-bold" data-tooltip="SIGN UP">SIGN UP</a></div>
      </div>
      <!-- NAV ELEMENTS UL WRAPPER END -->
      <!-- LOGIN SIGNUP BUTTON WRAPPER START -->
      <div class="hidden md:login_signup md:text-black md:flex">
        <div class="btns"><a href="#" class=" font-bold" data-tooltip="LOGIN">LOGIN</a></div>
        <div class="btns"><a href="#" class=" font-bold" data-tooltip="SIGN UP">SIGN UP</a></div>
      </div>
      <!-- LOGIN SIGNUP BUTTON WRAPPER END -->
    </div>
    <!-- NAVBAR WRAPPER END -->
  </div>
</template>

