<script>
export default {
    name:'Navbar',
    methods:{
     burgerFunc:function()  
     {
        //  const burger_btn=document.querySelector('.burger-wrapper');
        //  const burger=document.querySelector('.burger');
        // this.$refs.burger_wrapper_ref.addEventListener('click',()=>{
        // burger_btn.classList.toggle('open');
        // console.log('clicked');
        //console.log(burger.childNodes[1].children[0])
        // console.log(this.$refs.burger_wrapper_ref);
        // console.log(this.$refs.burger_ref.children[0].childNodes[0]);
        for(let i=0;i<=3;i++)
        {
            if(i==1 || i==2){
               this.$refs.burger_ref.children[0].childNodes[i].classList.toggle("opacity-0");
                this.$refs.burger_ref.children[0].childNodes[i].classList.toggle("w-0");
            }      
        }
        this.$refs.burger_ref.children[0].childNodes[3].classList.toggle("transform");
        this.$refs.burger_ref.children[0].childNodes[3].classList.toggle("-top-3");
        this.$refs.burger_ref.children[0].childNodes[3].classList.toggle("rotate-45");
        this.$refs.burger_ref.children[0].childNodes[0].classList.toggle("transform");
        this.$refs.burger_ref.children[0].childNodes[0].classList.toggle("top-3");
        this.$refs.burger_ref.children[0].childNodes[0].classList.toggle("-rotate-45");
        this.$refs.navbar_elements.classList.toggle('hidden');
        this.$refs.login_signup_mobile_btns.classList.toggle('hidden');
    // })
     }
     },
    props:['scroller'], //THE SCROLLER BOOLEAN VALUE true or false from Home Component
    data(){
        return{
            
        }
    }
    
}
</script>
   

<style lang="scss">
.burger-wrapper {
  -webkit-tap-highlight-color: transparent;
}
.login_signup_mobile,.nav_elements{
  -webkit-tap-highlight-color: transparent;
}
a {
  position: relative;
}
.nav_elements a::before {
  content: "";
  background-color: rgb(0, 185, 231);
  width: 15px;
  height: 4px;
  position: absolute;
  top: 100%;
  left: 0;
  transition: all ease-in 0.2s;
}
.nav_elements a::after {
  content: "";
  background-color: rgba(255, 117, 4, 0.87);
  width: 15px;
  height: 4px;
  position: absolute;
  top: 100%;
  right: 0;
  transition: all ease-in 0.2s;
}
.nav_elements .router-link-active::before,.nav_elements a:hover::before {
  content: "";
  background-color: rgb(0, 185, 231);
  transform: translateX(100%);
  width: 50%;
  height: 4px;
  position: absolute;
  top: 100%;
  left: 0;
}
.nav_elements .router-link-active::after,.nav_elements a:hover::after {
  content: "";
  background-color: rgba(255, 117, 4, 0.87);
  transform: translateX(-100%);
  width: 50%;
  height: 4px;
  position: absolute;
  top: 100%;
  right: 0;
}
.login_signup{
    display: flex;
}
.btns a{
    background: yellow;
    padding: 0.5rem;
    margin: 0.2rem;
    position: relative;
    margin-right: 0.6rem;
    letter-spacing: 0.2rem;
}
.btns a::before{
    position: absolute;
    content: attr(data-tooltip);
    background-color: rgba(255, 117, 4,1);
    top: 0;
    left:0;
    width:100%;
    height:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all ease-in 0.25s;
    transform-origin: top;
    transform: rotateX(270deg);
}
.btns a:hover::before{
   transform: rotateX(0deg);
}
.btns a::after{
    position: absolute;
    content: "CLICK";
    background-color: rgb(0, 185, 231);
    top: 0;
    left:0;
    width:100%;
    height:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all ease-in 0.5s;
    transform-origin: bottom;
    transform: rotateX(270deg);
}
.btns a:hover::after{
   transform: rotateX(0deg);
}
.logo_text{
  position:relative;
  color:white;
  &::before{
  content: 'share_zone';
  text-transform: uppercase;
  position:absolute;
  top: 0;
  left: 0;
  color:rgba(255, 117, 4,1);
  transition: all 0.5s;
  width: 0%;
  overflow: hidden;
  }
  &:hover{
  &::before{
       width: 100%;
     }
   }
}
</style>
