<template>
    <div class="Home bg-background_yellow">
        <div @scroll="onScroll" class="header-wrapper">
              <NavbarComp :scroller="scrolled" />
        </div>   
        <div class="extra">
        </div>
    </div>
</template>

<script>
import Navbar from './../components/Navbar.vue'
export default {
    name:'Home',
    data(){
        return{
            scrolled: false
        }
    },
    components:{
        NavbarComp:Navbar
    },
    methods:{
        onScroll () {
            this.scrolled = window.scrollY > 0;
            console.log(this.scrolled);
        }
    },
    created () {
        window.addEventListener('scroll', this.onScroll);
    },
    unmounted () {
        window.removeEventListener('scroll', this.onScroll);
    },
}   
</script>

<style  lang="scss">
.Home{
    width: 100%;
    height: 100vh;
}
</style>